	module.exports = {"data":{"markdownRemark":{"headings":[{"value":"Summary","depth":2},{"value":"Exploration","depth":2},{"value":"Research","depth":2},{"value":"Final","depth":2}],"html":"<h2>Summary</h2>\n<p>This project explores how touch and tactility can be applied within design to create meaning. The novel <em>The Lord of the Flies</em> is the vehicle in which this experimentation takes place to examine how a classic story can be adapted for a modern audience that demands interactivity. </p>\n<h2>Exploration</h2>\n<p>The written word has been around ever since humans have felt the need to express themselves. From cave drawings, to paper, to tablets, the written word has been around to communicate, to enlighten, to instruct and to entertain. There is something wonderful and thrilling about holding a book in one’s hands and turning the pages, yet today with electronic books, tablets and computers, the novel’s time in its original form is quickly becoming obsolete. Book stores are shutting their doors and libraries have had to reinvent themselves by offering new and faster methods of information through technologies in the place of books. So where is this all heading? Perhaps to the destruction of the novel as we once knew it. The dilemma then becomes how to reinvent the book so that this reality can be reversed and a new generation can once again experience the pleasures of holding a book but in a new and innovative way. The objective here is to integrate tactility and a strong visual element to reawaken our interest in books. This can be undertaken by focusing on how we physically and emotionally interact with texts so as to become active readers rather than passive ones by employing touch and tactility design. </p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 60.333333333333336%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"\"All around him the long scar smashed into the jungle\" page from Disassembling the Novel\"\n        title=\"\"\n        src=\"/static/rtb_scar-186874bb270543997125ba06f4e467fe-755ae.jpg\"\n        srcset=\"/static/rtb_scar-186874bb270543997125ba06f4e467fe-f07fc.jpg 125w,\n/static/rtb_scar-186874bb270543997125ba06f4e467fe-9d06f.jpg 250w,\n/static/rtb_scar-186874bb270543997125ba06f4e467fe-755ae.jpg 500w,\n/static/rtb_scar-186874bb270543997125ba06f4e467fe-63ca3.jpg 750w,\n/static/rtb_scar-186874bb270543997125ba06f4e467fe-9b9a3.jpg 1000w,\n/static/rtb_scar-186874bb270543997125ba06f4e467fe-f5eb2.jpg 1200w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<h2>Research</h2>\n<p>In order to test this theory I selected a well know and beloved work of literature <em>The Lord of the Flies</em>. This novel has been around since 1954 and tells the disastrous story of a group of boys shipwrecked on a deserted island as war wages on outside. While it has all the elements of a great novel, it is the perfect example of how a wonderful read can be neglected due to how the novel is presented. The story is reimagined as an interactive text through tactile touch. The novel has been deconstructed from its original form and reassembled in an exciting new manner which meets the needs of a generation that demands stimulation, excitement, and grabs both the attention and imagination. Paper textures, die cuts, and embossing are all utilized to reimagine some of the major themes and ideas within the novel using tactility. Using texture as a way to communicate pushes how materials and the way in which they are used, can convey a message. While in the reader’s hands the novel comes alive, through the textures, the sights and feelings it provokes, and the physical pleasure of the book itself. In this way it is a reinvention of the physical book and hints at the direction books can take. </p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 67.10000000000001%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"\"His mind was crowded with memories\" page from Disassembling the Novel\"\n        title=\"\"\n        src=\"/static/rtb_circles-a827525789da6e2fd4caca6a3d5b14b4-755ae.jpg\"\n        srcset=\"/static/rtb_circles-a827525789da6e2fd4caca6a3d5b14b4-f07fc.jpg 125w,\n/static/rtb_circles-a827525789da6e2fd4caca6a3d5b14b4-9d06f.jpg 250w,\n/static/rtb_circles-a827525789da6e2fd4caca6a3d5b14b4-755ae.jpg 500w,\n/static/rtb_circles-a827525789da6e2fd4caca6a3d5b14b4-63ca3.jpg 750w,\n/static/rtb_circles-a827525789da6e2fd4caca6a3d5b14b4-9b9a3.jpg 1000w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<h2>Final</h2>\n<p>This reimagined <em>Lord of the Flies</em> is an example of what the future of the book may be. As technologies become even more complex, offering all manner of information, it would be a tragedy if the written word in novel form were to disappear. Through designing for touch, we can keep pace with this need for interaction and reinvent the book through innovation and new designs.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 69.4%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Pig head with vines, page from Disassembling the Novel \"\n        title=\"\"\n        src=\"/static/rtb_pig-844f5c29305bcddc202179104c48aa0f-755ae.jpg\"\n        srcset=\"/static/rtb_pig-844f5c29305bcddc202179104c48aa0f-f07fc.jpg 125w,\n/static/rtb_pig-844f5c29305bcddc202179104c48aa0f-9d06f.jpg 250w,\n/static/rtb_pig-844f5c29305bcddc202179104c48aa0f-755ae.jpg 500w,\n/static/rtb_pig-844f5c29305bcddc202179104c48aa0f-63ca3.jpg 750w,\n/static/rtb_pig-844f5c29305bcddc202179104c48aa0f-9b9a3.jpg 1000w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>","frontmatter":{"title":"Disassembling the Novel","graduate":"Meaghan Gray","category":"Book Design","category2":null,"category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/static/rtb_cover-0542f1dacc6dcdba7b89902cd54e2728-75e94.jpg","srcSet":"/static/rtb_cover-0542f1dacc6dcdba7b89902cd54e2728-7516d.jpg 375w,\n/static/rtb_cover-0542f1dacc6dcdba7b89902cd54e2728-b4e9d.jpg 750w,\n/static/rtb_cover-0542f1dacc6dcdba7b89902cd54e2728-75e94.jpg 1000w","sizes":"(max-width: 1000px) 100vw, 1000px"}}}}}},"pathContext":{"slug":"/work/disassembling-the-novel/","name":"/Disassembling the Novel/"}}

